
@import "../scss/mixin.scss";
$active-color: #409eff;
.ld-checkbox{
    display: inline-flex;
    align-items: center;
    color: #606266;
    cursor: pointer;
    @include clear-default-touch-style;
    .checkbox-sel{
        position: relative;
        display: inline-block;
        width: 14px;
        height: 14px;
        border: 1px solid #dcdfe6;
        border-radius: 2px;
        background-color: #fff;
        &::after{
            position: absolute;
            top: 50%;
            left: 50%;
            width: 3px;
            height: 7px;
            border: 1px solid #fff;
            border-left: 0;
            border-top: 0;
            content: '';
            transform: translate(-50%, -50%) rotate(45deg) scaleY(0);
            transition: transform .15s ease-in .05s;
        }
    }
    .label{
        padding: 0 8px;
        user-select: none;
    }
    .input-checkbox{
        display: none;
    }
    &.active, &.intermediate{
        color: $active-color;
        .checkbox-sel{
            background-color: $active-color;
            border-color: $active-color;
        }
    }
    // 选中状态
    &.active{
         .checkbox-sel{
            &::after{
                transform: translate(-50%, -50%) rotate(45deg) scaleY(1);
            }
         }
    }
    // 中间状态
    &.intermediate{
        .checkbox-sel{
            &::after{
                width: 12px;
                height: 2px;
                border-color: transparent;
                background-color: #fff;
                transform: translate(-50%, -50%) scale(.5);
                transition: unset;
            }
        }
    }
    // 禁用状态
    &.disabled{
        pointer-events: none;
        cursor: not-allowed;
        color: #a8abb2;
        .checkbox-sel{
            background-color: #f2f6fc;
            border-color: #dcdfe6;
            &::after{
                border-color: #a8abb2;
            }
        }
    }
}


