
@import "../scss/mixin.scss";
.ld-pagination{
    display: flex;
    list-style: none;
    justify-content: center;
    margin: 0;
    padding: 0;
    .item{
        width: 28px;
        height: 28px;
        line-height: 28px;
        border-radius: 6px;
        text-align: center;
        border: 1px solid #e8e8f0;
        color: #9999a3;
        cursor: pointer;
        @include clear-default-touch-style;
        a{
            color: inherit;
            text-decoration: none;
        }
        &:not(&:first-child){
            margin-left: 10px;
        }
        &:hover{
            border-color: #2886f0;
            color: #2886f0;
        }
        &.active{
            background-color: #2886f0;
            border-color: #2886f0;
            color: #fff;
            &:hover{
                color: #fff;
            }
        }
        &.more{
            border-color: transparent;
            pointer-events: none;
        }
        &.none{
            background-color: #f6f6f6;
            border-color: #f6f6f6;
            pointer-events: none;
            cursor: not-allowed;
            &.prev, &.next{
                &::after{
                    border-left-color: #aaa;
                    border-bottom-color: #aaa;
                }
            }
        }
    }
}


